<template>
    <div id="register" class="customize_scrollbar">
        <b-container fluid>
            <b-row align-h="center" class="input-block">
                <b-col cols="auto">
                    <span class="title">選擇欲查看之日誌總類：</span>
                    <b-form-select
                        v-model="logTypeSelected"
                        :options="logTypeOptions"
                        style="width: 200px; border-color: #06122a"
                        class="publicText publicLightBG publicBorder"
                        size="md"
                        @change="
                            logList = [];
                            _logTypeOnChange($event);
                        "
                    >
                        <template v-slot:first>
                            <b-form-select-option :value="null" disabled
                                >-- 請選擇一個選項 --</b-form-select-option
                            >
                        </template>
                    </b-form-select>
                </b-col>
                <b-col cols="auto">
                    <span class="title">選擇欲查看之檔案：</span>
                    <b-form-select
                        v-model="logFileSelected"
                        :options="logFileOption[logTypeSelected]"
                        style="width: 300px; border-color: #06122a"
                        class="publicText publicLightBG publicBorder"
                        size="md"
                        @change="_logFileOnChange($event)"
                    >
                        <template v-slot:first>
                            <b-form-select-option :value="null" disabled
                                >-- 請選擇一個選項 --</b-form-select-option
                            >
                        </template>
                    </b-form-select>
                </b-col>
                <b-col cols="auto">
                    <template v-if="showTime">
                        <span class="title">日期：</span>
                        <b-form-input
                            class="text publicText publicLightBG publicBorder"
                            type="number"
                            v-model="startTime"
                            min="1"
                            max="31"
                            step="10"
                        ></b-form-input>
                        ~
                        <b-form-input
                            class="text publicText publicLightBG publicBorder"
                            type="number"
                            v-model="endTime"
                            min="1"
                            max="31"
                            step="10"
                        ></b-form-input>
                    </template>
                </b-col>
                <b-col cols="auto">
                    <button
                        type="button"
                        class="btn submit publicBlueBG-level2"
                        :disabled="
                            logTypeSelected == '' || logFileSelected == ''
                        "
                        @click.prevent="_submit"
                    >
                        查詢
                    </button>
                </b-col>
                <b-col sm="12">
                    <div
                        id="loggingBlock"
                        class="mt-3 customize_scrollbar publicBorder publicBlueBG-level3"
                        v-if="logList.length != 0"
                    >
                        <template
                            v-if="
                                logTypeSelected === 'PaaS_LOG' &&
                                thisApiGetLog === 'PaaS_LOG'
                            "
                        >
                            <b-table
                                hover
                                borderless
                                sticky-header="550px"
                                responsive="xl"
                                :items="logList"
                                :fields="paasLogFields"
                                id="paaslogTable"
                                class="paaslogTable customize_scrollbar"
                                :per-page="perPage"
                                :current-page="currentPage"
                            >
                                <template v-slot:cell(show_details)="row">
                                    <b-button
                                        size="sm"
                                        @click="row.toggleDetails"
                                        class="mr-2"
                                        >{{
                                            row.detailsShowing
                                                ? "隱藏"
                                                : "顯示"
                                        }}訊息</b-button
                                    >
                                </template>

                                <template v-slot:row-details="row">
                                    <!-- <b-card>
                                    <b-row class="mb-2">
                                                    <b-col class="text-sm-left">
                                                        
                                                    </b-col>
                                                </b-row> -->
                                    <jsonView
                                        v-if="
                                            typeof JSON.parse(
                                                row.item.message
                                            ) != 'string'
                                        "
                                        :data="JSON.parse(row.item.message)"
                                        :theme="''"
                                        :deep="3"
                                        :iconStyle="'triangle'"
                                        :fontSize="15"
                                        :lineHeight="24"
                                        :closed="false"
                                        :iconColor="[]"
                                    />
                                    <p v-else>{{ row.item.message }}</p>
                                    <!-- <b-button
                                                    size="sm"
                                                    @click="row.toggleDetails"
                                                    >Hide Message</b-button
                                                >
                                    </b-card> -->
                                </template>
                            </b-table>
                        </template>
                        <template
                            v-if="
                                logTypeSelected === 'uWSGI_LOG' &&
                                thisApiGetLog === 'uWSGI_LOG'
                            "
                        >
                            <b-table
                                :items="logList"
                                :per-page="perPage"
                                :current-page="currentPage"
                                thead-class="hidden_header"
                                borderless
                            ></b-table>
                            <!-- <div v-for="(item, key) in logList" :key="key">
                                <pre
                                    v-for="(item, key) in logList"
                                    :key="key"
                                    class="customize_scrollbar"
                                    >{{ item }}</pre
                                >
                            </div> -->
                        </template>
                    </div>
                    <h3 v-else class="publicText mt-2 ml-2">無資料</h3>
                    <b-col class="mt-3 text-center" cols="auto">
                        <b-pagination
                            v-if="logList.length != 0"
                            v-model="currentPage"
                            :total-rows="paginationRows"
                            :per-page="perPage"
                            align="center"
                            last-number
                        ></b-pagination>
                    </b-col>
                </b-col>
            </b-row>
        </b-container>
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import jsonView from "@/components/json-view/index.vue";
export default {
    name: "register",
    data() {
        let vm = this;
        return {
            logTypeOptions: [], //log type option
            logTypeSelected: "uWSGI_LOG", //log type option selected

            logFileOption: [], // save all log\file list(include paas&uwsgi)
            logFileSelected: "", //log file option selected

            paasLogFields: [
                {
                    key: "upload_at",
                    label: "上傳時間",
                    sortable: false,
                    thClass: "publicTH",
                },
                {
                    key: "acstime",
                    label: "發生錯誤時間",
                    sortable: true,
                    thClass: "publicTH",
                },
                {
                    key: "levelname",
                    label: "錯誤等級",
                    sortable: true,
                    thClass: "publicTH",
                },
                {
                    key: "filename",
                    label: "發生錯誤檔案",
                    sortable: false,
                    thClass: "publicTH",
                },
                {
                    key: "lineno",
                    label: "發生錯誤檔案行號",
                    sortable: false,
                    thClass: "publicTH",
                },
                {
                    key: "process",
                    label: "進程編號",
                    sortable: false,
                    thClass: "publicTH",
                },
                {
                    key: "thread",
                    label: "線程編號",
                    sortable: false,
                    thClass: "publicTH",
                },
                {
                    key: "show_details",
                    label: "錯誤訊息",
                    sortable: false,
                    thClass: "publicTH",
                },
            ],
            startTime: 1,
            endTime: 31,
            realStartTime: 0,
            realEndTime: 0,
            showTime: false,
            logList: [],
            currentPage: 1,
            perPage: 100,
            thisApiGetLog: "",
        };
    },
    watch: {
        "clickChild.child_name": {
            handler(value) {
                let vm = this;
                console.log("@@@@@@@");
                console.log(vm.clickChild);
                console.log(value);
                if (value.split("_")[0] === "register") {
                    //定位至選擇點
                    this.$el.querySelector("#" + value).scrollIntoView({
                        behavior: "smooth", // 平滑过渡
                        block: "start", // 上边框与视窗顶部平齐。默认值
                    });
                }
            },
        },
    },
    updated() {
        // 聊天定位到底部
        //https://zhuanlan.zhihu.com/p/89906315
        //https://lianjy357.github.io/baymax/2019/04/24/Vue%E8%81%8A%E5%A4%A9%E6%A1%86%E9%BB%98%E8%AE%A4%E6%BB%9A%E5%8A%A8%E5%88%B0%E5%BA%95%E9%83%A8/
        // if (this.logList.length != 0) {
        //     if (this.selected === "PaaS_LOG") {
        //         this.$nextTick(() => {
        //             let ele = document.getElementById("paaslogTable");
        //             ele.scrollTop = ele.scrollHeight;
        //             console.log(ele.scrollTop);
        //             console.log(ele.scrollHeight);
        //         });
        //     } else {
        //         let ele = document.getElementById("loggingBlock");
        //         ele.scrollTop = ele.scrollHeight;
        //     }
        //     console.log("======================");
        // }
    },
    components: {
        jsonView,
    },
    computed: {
        ...mapGetters({
            axiosResult: "commonaxios/get_axiosResult",
            clickChild: "featurelistbar/get_clickChild",
        }),
        paginationRows() {
            return this.logList.length;
        },
    },
    created() {
        this._getLogFile();
    },
    mounted() {},
    methods: {
        ...mapActions({
            axiosGetAction: "commonaxios/axiosGetAction",
            axiosPostAction: "commonaxios/axiosPostAction",
            setthisSortData: "sort/set_thisSortData",
            setFeatureMainData: "featuremain/set_featureMainData",
            setClickRoute: "featuremain/set_clickRoute",
            toggleSideBarStatus: "featuresidebar/toggle_sideBarStatus",
            setSetTimeOutAlertMsg: "alertmodal/set_setTimeOutAlertMsg",
            toggleSetTimeOutAlertModal:
                "alertmodal/toggle_settimeout_alertModal",
            toggleAlertModal: "alertmodal/toggle_alertModal",
        }),

        _getLogFile() {
            let vm = this;
            var params = {};
            this.toggleAlertModal(true);
            params["url"] =
                "api/PaaS/1.0/sysps/LogType/" + vm.$route.params.SYSTEM;
            params["urlparams"] = { GetLogFilesList: "Yes" };
            let anyerror = false;
            vm.axiosGetAction(params)
                .then(() => {
                    var result = vm.axiosResult;
                    console.log(result);
                    this.toggleAlertModal(false);
                    if (result["Response"] === "ok") {
                        let config = {
                            uWSGI_LOG: "uWSGI日誌",
                            PaaS_LOG: "API日誌",
                        };
                        result["FileType"].forEach((element) => {
                            let obj = {
                                value: element,
                                text: config[element],
                            };
                            vm.logTypeOptions.push(obj);
                        });
                        console.log(result["FileList"]);
                        Object.entries(result["FileList"]).forEach(
                            (element) => {
                                element[1].sort();
                            }
                        );
                        console.log(result["FileList"]);
                        vm.logFileOption = result["FileList"];

                        vm.logFileSelected =
                            result["FileList"]["uWSGI_LOG"][
                                result["FileList"]["uWSGI_LOG"].length - 1
                            ];
                        //一進頁面直接查uwsgi第一份log檔
                        this._submit();
                    } else {
                        vm.setSetTimeOutAlertMsg(result["Response"]);
                        anyerror = true;
                    }
                })
                .catch(function (err) {
                    console.log(err);
                    vm.setSetTimeOutAlertMsg(err);
                    anyerror = true;
                })
                .finally(() => {
                    if (anyerror) vm.toggleSetTimeOutAlertModal();
                });
        },

        _submit() {
            let vm = this;
            this.toggleAlertModal(true);
            console.log(vm.logFileSelected);
            console.log(vm.startTime);
            console.log(vm.endTime);
            vm.logList = [];
            var params = {};
            let anyerror = false;
            vm.thisApiGetLog = vm.logTypeSelected;
            if (vm.logTypeSelected === "uWSGI_LOG") {
                params["url"] =
                    "api/PaaS/1.0/sysps/LogContent/" + vm.logFileSelected;
                vm.axiosGetAction(params)
                    .then(() => {
                        this.toggleAlertModal(false);
                        var result = vm.axiosResult;
                        console.log(result);
                        if (result["Response"] === "ok") {
                            vm.logList = result["FileContent"];
                        } else {
                            vm.setSetTimeOutAlertMsg(result["Response"]);
                            vm.toggleSetTimeOutAlertModal();
                        }
                    })
                    .catch(function (err) {
                        console.log(err);
                        vm.setSetTimeOutAlertMsg(err);
                        anyerror = true;
                    })
                    .finally(() => {
                        if (anyerror) vm.toggleSetTimeOutAlertModal();
                    });
            } else {
                if (
                    vm.startTime < vm.realStartTime ||
                    vm.startTime > vm.realEndTime ||
                    vm.endTime < vm.realStartTime ||
                    vm.endTime > vm.realEndTime
                ) {
                    vm.toggleAlertModal(false);
                    vm.setSetTimeOutAlertMsg("日期填寫錯誤");
                    vm.toggleSetTimeOutAlertModal();
                    return;
                }
                console.log("%%%%%%%%%%%");
                let temptime = vm.logFileSelected.split("_");
                temptime = temptime[temptime.length - 1];
                params["url"] =
                    "api/" +
                    vm.$route.params.SYSTEM +
                    "/2.5/myps/Sensor/SqlSyntax?uid=@sapido@PaaS&dbName=paas_dashboard&getSqlSyntax=yes";
                let post_data = {
                    condition_1: {
                        fields: "",
                        where: "",
                        orderby: "",
                        limit: "",
                        symbols: "",
                        table: vm.logFileSelected,
                        intervaltime: {
                            upload_at: [
                                [
                                    temptime.substr(0, temptime.length - 2) +
                                        "-" +
                                        temptime.substr(
                                            temptime.length - 2,
                                            2
                                        ) +
                                        "-" +
                                        vm.startTime +
                                        " 00:00:00",
                                    temptime.substr(0, temptime.length - 2) +
                                        "-" +
                                        temptime.substr(
                                            temptime.length - 2,
                                            2
                                        ) +
                                        "-" +
                                        vm.endTime +
                                        " 23:59:59",
                                ],
                            ],
                        },
                        union: "",
                        subquery: "",
                    },
                };
                console.log(post_data);
                params["postdata"] = post_data;
                let anyerror = false;
                vm.axiosPostAction(params)
                    .then(() => {
                        var result = vm.axiosResult;
                        console.log(result);
                        vm.toggleAlertModal(false);
                        if (result["Response"] === "ok") {
                            vm.logList = result["QueryTableData"];
                        } else {
                            vm.setSetTimeOutAlertMsg(result["Response"]);
                            vm.toggleSetTimeOutAlertModal();
                        }
                    })
                    .catch(function (err) {
                        console.log(err);
                        vm.setSetTimeOutAlertMsg(err);
                        anyerror = true;
                    })
                    .finally(() => {
                        if (anyerror) vm.toggleSetTimeOutAlertModal();
                    });
            }
        },

        _logTypeOnChange(event) {
            console.log(event);
            var vm = this;
            if (event === "PaaS_LOG") {
                vm.showTime = true;
            } else {
                vm.showTime = false;
            }
        },

        _logFileOnChange(event) {
            console.log(event);
            var vm = this;
            if (vm.logTypeSelected === "PaaS_LOG") {
                let temptime = event.split("_");
                temptime = temptime[temptime.length - 1];
                let tempyear = temptime.substr(0, temptime.length - 2);
                let tempmonth = temptime.substr(temptime.length - 2, 2);
                console.log(tempyear);
                console.log(tempmonth);
                vm.realStartTime = new Date(
                    tempyear,
                    parseInt(tempmonth) - 1,
                    1
                ).getDate();
                vm.realEndTime = new Date(
                    tempyear,
                    parseInt(tempmonth),
                    0
                ).getDate();
                vm.startTime = vm.realStartTime;
                vm.endTime = vm.realEndTime;
            }
        },

        _onResize() {
            let vm = this;
            console.log(window.innerWidth);
            if (window.innerWidth < 1200) {
                // vm.resize_style["margin-top"] =
                //     vm.$refs.leftblock.clientHeight + "px";
                vm.searchBarFixed = true;
            } else {
                vm.searchBarFixed = false;
                // vm.resize_style["margin-top"] = "0px";
            }
        },

        _handleScroll() {
            let scrollTop =
                window.pageYOffset ||
                document.documentElement.scrollTop ||
                document.body.scrollTop;
            console.log(scrollTop);

            let offsetTop = document.querySelector("#leftblock").offsetTop;
            console.log(offsetTop);
            if (window.innerWidth < 1200) {
                if (scrollTop > offsetTop) {
                    if (!this.searchBarFixed) {
                        setTimeout(() => {
                            this.searchBarFixed = true;
                        }, 50);
                    } else {
                        this.searchBarFixed = true;
                    }
                } else {
                    this.searchBarFixed = false;
                }
                this.monitorScrollTop = scrollTop;
            }

            this.$nextTick(() => {
                if (this.monitorScrollTop > scrollTop) {
                    console.log("~~~~");
                    this.leftblockShow = true;
                } else {
                    console.log("@@@@");
                    this.leftblockShow = false;
                }
            });
            this.monitorScrollTop = scrollTop;
        },

        //資料reset
        _reset(keep) {
            var def = this.$options.data();
            for (var i = 0; i < keep.length; i++) {
                def[keep[i]] = this[keep[i]];
            }
            Object.assign(this.$data, def);
            //https://codepen.io/karimcossutti/pen/ObXyKq
        },
    },
};
</script>

