            tri_comb_aprs: { name: "三點組合空壓" },
            pko_sfpin_aprs: { name: "前沖安全銷空壓" },
            clp_spr_aprs: { name: "夾仔空壓彈簧空壓" },
            dko_sfpin_aprs: { name: "後通安全銷空壓" },
            trim_aprs: { name: "整頭空壓" },
            fedw_aprs: { name: "送料輪空壓" },
            clg_aprs: { name: "冷卻油管空壓" },
            postub_aprs: { name: "後牙管空壓" },
            trx_jaw_aprs: { name: "三軸夾爪空壓" },
            clp_aprs: { name: "夾仔空壓" },
            mcg_revsp: { name: "機器轉速" },
            aut_level: { name: "副油箱油位" },
            clut_brk_cnt: { name: "曲軸煞停角度" },
          },
        },
        emeter: {
          name: "智慧電表",
          sensor: {
            current: { name: "當前電流值" },
            cur_cubr: { name: "電流不平衡率" },
            cur_chdr: { name: "電流諧波失真率" },
            voltage: { name: "當前電壓值" },
            volt_vubr: { name: "電壓不平衡率" },
            volt_chdr: { name: "電壓諧波失真率" },
            frequency: { name: "頻率" },
            TPF: { name: "總功率因數" },
            RELEC: { name: "實功電能" },
            RPWD_cur: { name: "實功功率需量 - 當前" },
            RPWD_pred: { name: "實功功率需量 - 預測" },
            RPWD_peak: { name: "實功功率需量 - 峰值" },
          },
        },
        vibBearing: {
          name: "振動連座軸承",
          sensor: {
            FFT: { name: "FFT" },
            vibspeed_x: { name: "振動速度－X軸" },
            vibspeed_y: { name: "振動速度－Y軸" },
            vibspeed_z: { name: "振動速度－Z軸" },
            FFT_config: { name: "FFT讀取設定" },
          },
        },
        vibMotor: {
          name: "振動主馬達",
          sensor: {
            FFT: { name: "FFT" },
            vibspeed_x: { name: "振動速度－X軸" },
            vibspeed_y: { name: "振動速度－Y軸" },
            vibspeed_z: { name: "振動速度－Z軸" },
            FFT_config: { name: "FFT讀取設定" },
          },
        },
      },
      deviceSelected: null,
      deviceOptions: [],
      noCollapse: true,
      updateMod: {
        main: false,
        emeter: false,
        vibBearing: false,
        vibMotor: false,
      },
    };
  },
  computed: {
    ...mapGetters({
      selectPage: "mesGetLogin/get_selectPage",
      selectPageName: "mesGetLogin/get_selectPageName",
      axiosResult: "mescommonaxios/get_axiosResult",
    }),
  },
  watch: {},
  methods: {
    ...mapActions({
      axiosAction: "mescommonaxios/axiosAction",
      setSetTimeOutAlertMsg: "alertmodal/set_setTimeOutAlertMsg",
      toggleSetTimeOutAlertModal: "alertmodal/toggle_settimeout_alertModal",
    }),
    queryData: function () {
      let vm = this;
      let device_name = "F01";
      var params = {};
      params["vuePage"] = vm.selectPage.toLowerCase();
      params["methods"] = "POST";
      params["whichFunction"] = "MainQuery";
      params["device_name"] = device_name;
      console.log(params);

      vm.axiosAction(params).then(() => {
        var result = vm.axiosResult;
        if (result["Response"] == "ok") {
          vm.settingData = result.QueryTableData[0];
          vm.deviceOptions = [result.QueryTableData[0].device_name];
        } else {
          console.log("error:" + result);
        }
      });
    },
    setSensorTable: function () {
      let vm = this;
      if (vm.deviceSelected) {
        let deviceName = vm.deviceSelected;
        let sensorList = JSON.parse(vm.settingData.sensor_list);
        let sensorErr = JSON.parse(vm.settingData.sensor_error);
        let sensorWarn = JSON.parse(vm.settingData.sensor_warn);

        let sensorListKey = Object.keys(sensorList);
        sensorListKey.forEach((tableName) => {
          let tableArr = [];
          let sensorKey = sensorList[tableName].sensor;
          let itemKey = Object.keys(sensorKey);
          itemKey.forEach((sensorItem) => {
            if (sensorErr[tableName]) {
              if (
                sensorErr[tableName][sensorItem] &&
                sensorWarn[tableName][sensorItem]
              ) {
                let tableVal = {
                  sensorPointVal: sensorItem,
                  sensorPoint: sensorKey[sensorItem]["name"],
                  errorVal: {
                    min: sensorErr[tableName][sensorItem]["min"],
                    max: sensorErr[tableName][sensorItem]["max"],
                    warnClass: false,
                    normalClass: true,
                  },
                  warnVal: {
                    min: sensorWarn[tableName][sensorItem]["min"],
                    max: sensorWarn[tableName][sensorItem]["max"],
                    warnClass: false,
                    normalClass: true,
                  },
                };
                tableArr.push(tableVal);
              } else {
                let tableVal = {
                  sensorPointVal: sensorItem,
                  sensorPoint: sensorKey[sensorItem]["name"],
                  errorVal: {
                    min: "",
                    max: "",
                    warnClass: false,
                    normalClass: true,
                  },
                  warnVal: {
                    min: "",
                    max: "",
                    warnClass: false,
                    normalClass: true,
                  },
                };
                tableArr.push(tableVal);
              }
            } else {
              let tableVal = {
                sensorPointVal: sensorItem,
                sensorPoint: sensorKey[sensorItem]["name"],
                errorVal: {
                  min: "",
                  max: "",
                  warnClass: false,
                  normalClass: true,
                },
                warnVal: {
                  min: "",
                  max: "",
                  warnClass: false,
                  normalClass: true,
                },
              };
              tableArr.push(tableVal);
            }
          });
          vm[tableName + "Table"] = tableArr;
        });
      } else {
        vm.setSetTimeOutAlertMsg("請選擇機台");
        vm.toggleSetTimeOutAlertModal();
      }
    },
    openUpdate: function (sensorTitle) {
      let vm = this;
      if (vm.mainTable.length == 0) {
        vm.setSetTimeOutAlertMsg("請選擇機台");
        vm.toggleSetTimeOutAlertModal();
      } else {
        vm.updateMod[sensorTitle] = true;
      }
    },
    confirmUpdate(sensorTitle) {
      let vm = this;
      let warnText = [];
      vm[sensorTitle + "Table"].forEach((item) => {
        if (item.errorVal.min != "" || item.errorVal.max != "") {
          if (
            (item.errorVal.min == "" && item.errorVal.max != "") ||
            (item.errorVal.min != "" && item.errorVal.max == "") ||
            Number(item.errorVal.min) >= Number(item.errorVal.max) ||
            isNaN(Number(item.errorVal.min)) ||
            isNaN(Number(item.errorVal.max))
          ) {
            item.errorVal.normalClass = false;
            item.errorVal.warnClass = true;
            warnText.push(true);
          } else {
            item.errorVal.normalClass = true;
            item.errorVal.warnClass = false;
          }
        }

        if (item.warnVal.min != "" || item.warnVal.max != "") {
          if (
            (item.warnVal.min == "" && item.warnVal.max != "") ||
            (item.warnVal.min != "" && item.warnVal.max == "") ||
            Number(item.warnVal.min) >= Number(item.warnVal.max) ||
            isNaN(Number(item.warnVal.min)) ||
            isNaN(Number(item.warnVal.max))
          ) {
            item.warnVal.normalClass = false;
            item.warnVal.warnClass = true;
            warnText.push(true);
          } else {
            item.warnVal.normalClass = true;
            item.warnVal.warnClass = false;
          }
        }
      });

      if (warnText.length > 0) {
        vm.setSetTimeOutAlertMsg("設定值 : 最大值請大於最小值");
        vm.toggleSetTimeOutAlertModal();
      } else {
        let deviceName = vm.deviceSelected;
        let sensorErrOri = JSON.parse(vm.settingData.sensor_error);
        let sensorWarnOri = JSON.parse(vm.settingData.sensor_warn);
        vm[sensorTitle + "Table"].forEach((item) => {
          if (item.errorVal.min != "" && item.errorVal.max != "") {
            if (sensorErrOri[sensorTitle]) {
              if (sensorErrOri[sensorTitle][item.sensorPointVal]) {
                sensorErrOri[sensorTitle][item.sensorPointVal].min =
                  item.errorVal.min;
                sensorErrOri[sensorTitle][item.sensorPointVal].max =
                  item.errorVal.max;
              } else {
                sensorErrOri[sensorTitle][item.sensorPointVal] = {
                  min: item.errorVal.min,
                  max: item.errorVal.max,
                };
              }
            } else {
              let newAddObj = {};
              if (sensorErrOri == "") {
                sensorErrOri = {};
                newAddObj[item.sensorPointVal] = {
                  min: item.errorVal.min,
                  max: item.errorVal.max,
                };
                sensorErrOri[sensorTitle] = newAddObj;
              } else {
                newAddObj[item.sensorPointVal] = {
                  min: item.errorVal.min,
                  max: item.errorVal.max,
                };
                sensorErrOri[sensorTitle] = newAddObj;
              }
            }
          }

          if (item.warnVal.min != "" && item.warnVal.max != "") {
            if (sensorWarnOri[sensorTitle]) {
              if (sensorWarnOri[sensorTitle][item.sensorPointVal]) {
                sensorWarnOri[sensorTitle][item.sensorPointVal].min =
                  item.warnVal.min;
                sensorWarnOri[sensorTitle][item.sensorPointVal].max =
                  item.warnVal.max;
              } else {
                sensorWarnOri[sensorTitle][item.sensorPointVal] = {
                  min: item.warnVal.min,
                  max: item.warnVal.max,
                };
              }
            } else {
              let newAddObj = {};
              if (sensorWarnOri == "") {
                sensorWarnOri = {};
                newAddObj[item.sensorPointVal] = {
                  min: item.warnVal.min,
                  max: item.warnVal.max,
                };
                sensorWarnOri[sensorTitle] = newAddObj;
              } else {
                newAddObj[item.sensorPointVal] = {
                  min: item.warnVal.min,
                  max: item.warnVal.max,
                };
                sensorWarnOri[sensorTitle] = newAddObj;
              }
            }
          }
        });

        console.log(JSON.stringify(sensorErrOri));
        console.log(JSON.stringify(sensorWarnOri));

        var params = {};
        params["vuePage"] = vm.selectPage.toLowerCase();
        params["methods"] = "POST";
        params["whichFunction"] = "settingUpdate";
        params["device_name"] = deviceName;
        params["data_sensor_err"] = sensorErrOri;
        params["data_sensor_warn"] = sensorWarnOri;
        vm.axiosAction(params).then(() => {
          var result = vm.axiosResult;
          if (result["Response"] == "ok") {
            vm.setSetTimeOutAlertMsg("設定成功");
            vm.toggleSetTimeOutAlertModal();
            vm.queryData();
            vm[sensorTitle + "Table"].forEach((item) => {
              item.errorVal.normalClass = true;
              item.errorVal.warnClass = false;
              item.warnVal.normalClass = true;
              item.warnVal.warnClass = false;
            });
            vm.updateMod[sensorTitle] = false;
          } else {
            console.log("error:" + result);
          }
        });
      }
    },
  },
  created: function () {
    let vm = this;
    vm.queryData();
  },
};
</script>
