<template>
  <div>
    <div class="col-12 row text-left">
      <div class="mx-2 mb-2" style="width: 9rem; display: inline-block">
        <p class style="margin-bottom: 0.625rem">機台</p>
        <b-form-select
          class=""
          style="
            max-width: 9rem;
            height: 2.375rem;
            line-height: 1.375rem;
            font-size: 0.875rem;
            border: 2px solid rgb(255, 255, 255);
            box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 3px 0px;
            color: rgb(95, 95, 95);
          "
          v-model="deviceSelected"
          :options="deviceOptions"
        >
          <!-- This slot appears above the options from 'options' prop -->
          <template #first>
            <b-form-select-option :value="null" disabled
              >-- 選擇機台 --</b-form-select-option
            >
          </template>
        </b-form-select>
      </div>
      <div class="mx-2 mb-2" style="width: 9rem; display: inline-block">
        <p class style="margin-bottom: 0.625rem">感測點</p>
        <b-form-select
          class=""
          style="
            max-width: 9rem;
            height: 2.375rem;
            line-height: 1.375rem;
            font-size: 0.875rem;
            border: 2px solid rgb(255, 255, 255);
            box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 3px 0px;
            color: rgb(95, 95, 95);
          "
          v-model="sensorSelected"
          :options="sensorOptions"
        >
          <!-- This slot appears above the options from 'options' prop -->
          <template #first>
            <b-form-select-option :value="null" disabled
              >-- 選擇感測點 --</b-form-select-option
            >
          </template>
          <!-- <b-form-select-option v-for="item in detectPoint" :key="item.index">{{
            item.name
          }}</b-form-select-option> -->
        </b-form-select>
      </div>

      <div class="mx-2 mb-2" style="width: 12rem; display: inline-block">
        <div><p class style="margin-bottom: 0.625rem">異常值</p></div>

        <b-form-input
          :class="{
            inputNormal: inputNormalActive,
            inputWarn: inputWarnActive[0],
          }"
          placeholder="最小值"
          v-model="errorValue.minValue"
          oninput="value=value.replace(/[^\d.-]/g,'')"
        ></b-form-input>
        <span class="ml-2 mr-2">~</span>
        <b-form-input
          :class="{
            inputNormal: inputNormalActive,
            inputWarn: inputWarnActive[1],
          }"
          placeholder="最大值"
          v-model="errorValue.maxValue"
          oninput="value=value.replace(/[^\d.-]/g,'')"
        ></b-form-input>
      </div>

      <div class="mb-2" style="width: 12rem; display: inline-block">
        <div><p class style="margin-bottom: 0.625rem">警示值</p></div>

        <b-form-input
          :class="{
            inputNormal: inputNormalActive,
            inputWarn: inputWarnActive[2],
          }"
          placeholder="最小值"
          v-model="warnValue.minValue"
          oninput="value=value.replace(/[^\d.-]/g,'')"
        ></b-form-input>
        <span class="ml-2 mr-2">~</span>
        <b-form-input
          :class="{
            inputNormal: inputNormalActive,
            inputWarn: inputWarnActive[3],
          }"
          placeholder="最大值"
          v-model="warnValue.maxValue"
          oninput="value=value.replace(/[^\d.-]/g,'')"
        ></b-form-input>
      </div>

      <div class="mx-2 mb-2" style="width: 4.25rem; display: inline-block">
        <b-button
          class="p-0"
          style="
            margin-top: 38px;
            background-color: #536172;
            border-color: #536172;
            width: 4.25rem;
            height: 2rem;
            font-size: 1.125rem;
          "
          @click="settingConfirm"
          >設定</b-button
        >
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {
      settingData: [],
      settingDataTest: {
        main: {
          name: "主機台",
          sensor: {
            mcks_op_temp: { name: "主曲軸操作側溫度" },
            mcks_cus_temp: { name: "主曲軸剪斷側溫度" },
            msld_op_temp: { name: "主滑台操作側溫度" },
            msld_cus_temp: { name: "主滑台剪斷側溫度" },
            stnk_lub_temp: { name: "副油箱潤滑油溫度" },
            mcks_op_flow: { name: "主曲軸操作側流量" },
            mcks_cus_flow: { name: "主曲軸剪斷側流量" },
            msld_op_flow: { name: "主滑台操作側流量" },
            msld_cus_flow: { name: "主滑台剪斷側目前流量" },
            cus_lup_out_oprs: { name: "剪斷側潤滑泵輸出油壓" },
            msld_lup_out_oprs: { name: "主滑台潤滑泵輸出油壓" },
            cus_end_oprs: { name: "剪斷側油壓末端油壓" },
            msld_end_oprs: { name: "主滑台油壓末端油壓" },
            tri_comb_aprs: { name: "三點組合空壓" },
            pko_sfpin_aprs: { name: "前沖安全銷空壓" },
            clp_spr_aprs: { name: "夾仔空壓彈簧空壓" },
            dko_sfpin_aprs: { name: "後通安全銷空壓" },
            trim_aprs: { name: "整頭空壓" },
            fedw_aprs: { name: "送料輪空壓" },
            clg_aprs: { name: "冷卻油管空壓" },
            postub_aprs: { name: "後牙管空壓" },
            trx_jaw_aprs: { name: "三軸夾爪空壓" },
            clp_aprs: { name: "夾仔空壓" },
            mcg_revsp: { name: "機器轉速" },
            aut_level: { name: "副油箱油位" },
            clut_brk_cnt: { name: "曲軸煞停角度" },
          },
        },
        emeter: {
          name: "智慧電表",
          sensor: {
            current: { name: "當前電流值" },
            cur_cubr: { name: "電流不平衡率" },
            cur_chdr: { name: "電流諧波失真率" },
            voltage: { name: "當前電壓值" },
            volt_vubr: { name: "電壓不平衡率" },
            volt_chdr: { name: "電壓諧波失真率" },
            frequency: { name: "頻率" },
            TPF: { name: "總功率因數" },
            RELEC: { name: "實功電能" },
            RPWD_cur: { name: "實功功率需量 - 當前" },
            RPWD_pred: { name: "實功功率需量 - 預測" },
            RPWD_peak: { name: "實功功率需量 - 峰值" },
          },
        },
        vibBearing: {
          name: "振動連座軸承",
          sensor: {
            FFT: { name: "FFT" },
            vibspeed_x: { name: "振動速度－X軸" },
            vibspeed_y: { name: "振動速度－Y軸" },
            vibspeed_z: { name: "振動速度－Z軸" },
            FFT_config: { name: "FFT讀取設定" },
          },
        },
        vibMotor: {
          name: "振動主馬達",
          sensor: {
            FFT: { name: "FFT" },
            vibspeed_x: { name: "振動速度－X軸" },
            vibspeed_y: { name: "振動速度－Y軸" },
            vibspeed_z: { name: "振動速度－Z軸" },
            FFT_config: { name: "FFT讀取設定" },
          },
        },
      },
      errorValue: { minValue: "", maxValue: "" },
      warnValue: { minValue: "", maxValue: "" },
      deviceSelected: null,
      sensorSelected: null,
      deviceOptions: [],
      sensorOptions: [],
      inputNormalActive: true,
      inputWarnActive: [false, false, false, false],
    };
  },
  computed: {
    ...mapGetters({
      selectPage: "mesGetLogin/get_selectPage",
      selectPageName: "mesGetLogin/get_selectPageName",
      axiosResult: "mescommonaxios/get_axiosResult",
    }),
  },
  watch: {
    deviceSelected: {
      handler(value) {
        this.setSensorOption(value);
      },
    },
    sensorSelected: {
      handler(value) {
        this.setSensorVal(value);
      },
    },
  },
  methods: {
    ...mapActions({
      axiosAction: "mescommonaxios/axiosAction",
      setSetTimeOutAlertMsg: "alertmodal/set_setTimeOutAlertMsg",
      toggleSetTimeOutAlertModal: "alertmodal/toggle_settimeout_alertModal",
    }),
    settingConfirm: function () {
      let vm = this;
      let valueArr = [
        Number(vm.errorValue.minValue),
        Number(vm.errorValue.maxValue),
        Number(vm.warnValue.minValue),
        Number(vm.warnValue.maxValue),
      ];
      let nanArr = [];
      if (vm.deviceSelected == null) {
        vm.setSetTimeOutAlertMsg("請選擇機台");
        vm.toggleSetTimeOutAlertModal();
      } else if (vm.sensorSelected == null) {
        vm.setSetTimeOutAlertMsg("請選擇感測點");
        vm.toggleSetTimeOutAlertModal();
      } else {
        valueArr.forEach((item) => {
          if (isNaN(item) || item == "") {
            nanArr.push(true);
          } else {
            nanArr.push(false);
          }
        });

        vm.inputWarnActive = nanArr;
        if (nanArr.includes(true)) {
          vm.setSetTimeOutAlertMsg("數值輸入錯誤");
          vm.toggleSetTimeOutAlertModal();
        } else if (valueArr[0] >= valueArr[1]) {
          vm.inputWarnActive[1] = true;
          vm.setSetTimeOutAlertMsg("異常值:最大值應大於最小值");
          vm.toggleSetTimeOutAlertModal();
        } else if (valueArr[2] >= valueArr[3]) {
          vm.inputWarnActive[3] = true;
          vm.setSetTimeOutAlertMsg("警示值:最大值應大於最小值");
          vm.toggleSetTimeOutAlertModal();
        } else {
          let deviceName = vm.deviceSelected;
          let sensorTitle = vm.sensorSelected.sensorTitle;
          let sensorName = vm.sensorSelected.sensorName;
          let sensorErrOri = JSON.parse(vm.settingData.sensor_error);
          let sensorWarnOri = JSON.parse(vm.settingData.sensor_warn);

          //異常值
          if (sensorErrOri[sensorTitle]) {
            if (sensorErrOri[sensorTitle][sensorName]) {
              sensorErrOri[sensorTitle][sensorName].min =
                vm.errorValue["minValue"];
              sensorErrOri[sensorTitle][sensorName].max =
                vm.errorValue["maxValue"];
            } else {
              sensorErrOri[sensorTitle][sensorName] = {
                min: vm.errorValue["minValue"],
                max: vm.errorValue["maxValue"],
              };
            }
          } else {
            let typeVal = {};
            typeVal[sensorName] = {
              min: vm.errorValue["minValue"],
              max: vm.errorValue["maxValue"],
            };
            sensorErrOri[sensorTitle] = typeVal;
            console.log(JSON.stringify(sensorErrOri));
          }

          //警示值
          if (sensorWarnOri[sensorTitle]) {
            if (sensorWarnOri[sensorTitle][sensorName]) {
              sensorWarnOri[sensorTitle][sensorName].min =
                vm.warnValue["minValue"];
              sensorWarnOri[sensorTitle][sensorName].max =
                vm.warnValue["maxValue"];
            } else {
              sensorWarnOri[sensorTitle][sensorName] = {
                min: vm.warnValue["minValue"],
                max: vm.warnValue["maxValue"],
              };
            }
          } else {
            let typeVal = {};
            typeVal[sensorName] = {
              min: vm.warnValue["minValue"],
              max: vm.warnValue["maxValue"],
            };
            sensorWarnOri[sensorTitle] = typeVal;
            console.log(JSON.stringify(sensorWarnOri));
          }

          var params = {};
          params["vuePage"] = vm.selectPage.toLowerCase();
          params["methods"] = "POST";
          params["whichFunction"] = "settingUpdate";
          params["device_name"] = deviceName;
          params["data_sensor_err"] = sensorErrOri;
          params["data_sensor_warn"] = sensorWarnOri;
          vm.axiosAction(params).then(() => {
            var result = vm.axiosResult;
            if (result["Response"] == "ok") {
              vm.setSetTimeOutAlertMsg("設定成功");
              vm.toggleSetTimeOutAlertModal();
              vm.queryData();
            } else {
              console.log("error:" + result);
            }
          });
        }
      }
    },
    queryData: function () {
      let vm = this;
      let device_name = "F01";
      var params = {};
      params["vuePage"] = vm.selectPage.toLowerCase();
      params["methods"] = "POST";
      params["whichFunction"] = "MainQuery";
      params["device_name"] = device_name;
      console.log(params);

      vm.axiosAction(params).then(() => {
        var result = vm.axiosResult;
        if (result["Response"] == "ok") {
          vm.settingData = result.QueryTableData[0];
          vm.deviceOptions = [result.QueryTableData[0].device_name];
        } else {
          console.log("error:" + result);
        }
      });
    },
    setSensorOption: function (deviceName) {
      let vm = this;
      let sensorList = JSON.parse(vm.settingData.sensor_list);
      let sensorArr = [];

      let sensorKeysArr = Object.keys(sensorList);
      sensorKeysArr.forEach((item) => {
        let sensorOpt = Object.keys(sensorList[item].sensor);
        let optsArr = [];
        sensorOpt.forEach((sensorItem) => {
          let optObj = {
            value: { sensorTitle: item, sensorName: sensorItem },
            text: sensorList[item].sensor[sensorItem].name,
          };
          optsArr.push(optObj);
        });
        let mainLabel = {
          label: sensorList[item].name,
          options: optsArr,
        };
        sensorArr.push(mainLabel);
      });
      vm.sensorOptions = sensorArr;
    },
    setSensorVal(val) {
      let vm = this;
      let sensorTitle = val.sensorTitle;
      let sensorName = val.sensorName;
      let sensorErrOri = JSON.parse(vm.settingData.sensor_error);
      let sensorWarnOri = JSON.parse(vm.settingData.sensor_warn);
      vm.inputWarnActive = [false, false, false, false];

      if (sensorErrOri[sensorTitle] && sensorErrOri[sensorTitle][sensorName]) {
        vm.errorValue["minValue"] = sensorErrOri[sensorTitle][sensorName].min;
        vm.errorValue["maxValue"] = sensorErrOri[sensorTitle][sensorName].max;
      } else {
        vm.errorValue["minValue"] = "";
        vm.errorValue["maxValue"] = "";
      }
      if (sensorWarnOri[sensorTitle] && sensorWarnOri[sensorTitle][sensorName]) {
        vm.warnValue["minValue"] = sensorWarnOri[sensorTitle][sensorName].min;
        vm.warnValue["maxValue"] = sensorWarnOri[sensorTitle][sensorName].max;
      } else {
        vm.warnValue["minValue"] = "";
        vm.warnValue["maxValue"] = "";
      }
    },
  },
  created: function () {
    let vm = this;
    vm.queryData();
  },
};
</script>
<style scoped>
div,
p,
span {
  font-family: dfyuanstd-w7;
}

.inputNormal {
  max-width: 5rem;
  height: 2.375rem;
  line-height: 1.375rem;
  font-size: 0.875rem;
  border: 2px solid rgb(255, 255, 255);
  box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 3px 0px;
  color: rgb(95, 95, 95);
  display: inline-block;
}
.inputWarn {
  max-width: 5rem;
  height: 2.375rem;
  line-height: 1.375rem;
  font-size: 0.875rem;
  border: 2px solid rgb(255, 0, 0);
  box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 3px 0px;
  color: rgb(95, 95, 95);
  display: inline-block;
}
</style>