  created() {
    // if (this.mesSideBarSelect == null) {
    //   this.set_SideBarSelect(["elecboard", "heading", "heading4"]);
    // } else if (this.mesSideBarSelect.page != "elecboard") {
    //   this.set_SideBarSelect(["elecboard", "heading", "heading4"]);
    // } else {
    //   this._setElecboardGroup();
    // }

    window.addEventListener("resize", this._onResize_elecboard);
    this.changeSelectPage("elecboard");
    let vm = this;

    vm._clearTimer();
    // vm._setElecboardGroup();
    vm.device_array = ["F01", "F02", "F03", "F04", "F05"];
    vm._onResize_elecboard();
    window.addEventListener("beforeunload", () => {
      sessionStorage.setItem("mes_elecboard_nowPage", vm.nowPage);
      sessionStorage.setItem("mes_elecboard_deviceName", vm.deviceName);
      sessionStorage.setItem("mes_elecboard_process", vm.process_name);
    });
    vm.nowPage = sessionStorage.getItem("mes_elecboard_nowPage");
    vm.deviceName = sessionStorage.getItem("mes_elecboard_deviceName");
    vm.process_name = sessionStorage.getItem("mes_elecboard_process");
    if (vm.process_name == null) {
      vm.process_name = "成型機";
    }
    console.log(vm.nowPage);
    if (vm.nowPage != "elecboard" && vm.nowPage != "machstatus") {
      vm.nowPage = "elecboard";
    } else if (vm.nowPage == "machstatus") {
      vm.set_machineCameraData([
        {
          camera_name: "IPCAM1",
          url:
            "http://59.125.151.183:88/GetData.cgi?CH=1&WebUser=admin&WebPass=",
        },
      ]);
    }
    if (vm.deviceName == "") {
      vm.nowPage = "elecboard";
    }
    sessionStorage.removeItem("mes_elecboard_nowPage");
    sessionStorage.removeItem("mes_elecboard_deviceName");
    sessionStorage.removeItem("mes_elecboard_process");
    vm._getMachineData();

    console.log("-------------------------");
    console.log(this.$route.params.nowPage);
    if (this.$route.params.nowPage == "machstatus") {
      vm.nowPage = "machstatus";
      vm.deviceName = this.$route.params.device_data;
      vm.set_machineCameraData([
        {
          camera_name: "IPCAM1",
          url:
            "http://59.125.151.183:88/GetData.cgi?CH=1&WebUser=admin&WebPass=",
        },
      ]);
    }
  },
