    _getTotalDeviceStatus() {
      let vm = this;
      let totalDeviceStatus = {};
      vm.device_array.forEach((device_name) => {
        if (vm.result_Data[device_name] != undefined) {
          let machine_status = vm.result_Data[device_name].machine_status;
          if (machine_status == "-") {
            if (totalDeviceStatus["離線"] == undefined) {
              totalDeviceStatus["離線"] = 0;
            }
            totalDeviceStatus["離線"]++;
          } else if (machine_status == "異常") {
            if (totalDeviceStatus["異常"] == undefined) {
              totalDeviceStatus["異常"] = 0;
            }
            totalDeviceStatus["異常"]++;
          } else if (machine_status == "待機") {
            if (totalDeviceStatus["待機"] == undefined) {
              totalDeviceStatus["待機"] = 0;
            }
            totalDeviceStatus["待機"]++;
          } else if (machine_status == "運作") {
            if (totalDeviceStatus["運作"] == undefined) {
              totalDeviceStatus["運作"] = 0;
            }
            totalDeviceStatus["運作"]++;
          }
        } else {
          if (totalDeviceStatus["離線"] == undefined) {
            totalDeviceStatus["離線"] = 0;
          }
          totalDeviceStatus["離線"]++;
        }
      });
      console.log(totalDeviceStatus);
      vm.totalDeviceStatus = totalDeviceStatus;
    },

    _getMachineData() {
      let vm = this;
      // if (vm.selectPage != "elecboard") {
      //   return;
      // }
      var params = {};
      params["vuePage"] = vm.selectPage;
      params["methods"] = "GET";
      params["whichFunction"] = "MainQuery";
      // params["model"] = vm.mesSideBarSelect.model;
      // params["group_name"] = vm.group_name;
      vm.query_time = new Date();
      vm.axiosAction(params).then(() => {
        vm.searching = false;
        var result = vm.axiosResult;
        console.log(result);
        if (result.Response == "ok") {
          let result_data = result["QueryTableData"];
          // for (let i = 0; i < result_data.length; i++) {
          vm.result_Data[result_data[0]["device_name"]] = result_data[0];
          vm._getTotalDeviceStatus();
          // }
          vm.set_stacksData(result_data[1]["stack_bar_information"]);
          vm.machineHealthData.temp.table.items = result["sensor"].temp_data;
          vm.machineHealthData.flow.table.items = result["sensor"].flow_data;
          vm.machineHealthData.oilPressure.table.items =
            result["sensor"].oilPressure_data;
          vm.machineHealthData.airPressure1.table.items =
            result["sensor"].airPressure1_data;
          vm.machineHealthData.airPressure2.table.items =
            result["sensor"].airPressure2_data;
          vm.machineHealthData.oilTemp.table.items =
            result["sensor"].oilTemp_data;
          vm.machineHealthData.clutch.table.items =
            result["sensor"].clutch_data;
          vm.machineHealthData.oilLevel.value = result["sensor"].oilLevel_data;
          vm.machineHealthData.serverDriver.value =
            result["sensor"].serverDriver_data;
          vm.vibBearing.table.items = result["sensor"].vibBearing_data;
          vm.vibMotor.table.items = result["sensor"].vibMotor_data;
          vm.result_Data["F01"].machine_status =
            result["sensor"].machine_status;
          vm.result_Data["F01"].device_capacity_min =
            result["sensor"].device_capacity_min;
          vm.result_Data["F01"].device_stackBar =
            result["sensor"].stack_bar_information;
          vm.result_Data["F01"].device_now_count =
            result["sensor"].device_now_count;
          vm.result_Data["F01"].device_run_time =
            result["sensor"].device_run_time;
          vm.result_Data["F01"].device_activation =
            result["sensor"].device_activation;
          vm.result_Data["F01"].device_day_count =
            result["sensor"].device_day_count;
          vm.result_Data["F01"].wire_weight = result["sensor"].loadCell_data;
          vm.operateLogDataItem = result["sensor"].operatelog_information;
          vm.alarmHistoryDataItem = result["sensor"].alarmHistory_information;

          let relecChart_senTime = Object.values(
            result["sensor"].RELEC_chart
          ).map((item) => item.data_time);
          vm.relecChart.options[0].value.data.senTime = relecChart_senTime;
          let relecChart_senData = Object.values(
            result["sensor"].RELEC_chart
          ).map((item) => item.data);
          vm.relecChart.options[0].value.data.senData = relecChart_senData;

          vm.machineItem_select = result["sensor"]["machine_light_value"];
          vm.smartMeterChart.options[0].value.data.emeter_data[0].value =
            result["sensor"]["emeter_data"]["current"]["value"];
          vm.smartMeterChart.options[0].value.data.emeter_data[0].unit =
            result["sensor"]["emeter_data"]["current"]["unit"];
          vm.smartMeterChart.options[0].value.data.emeter_data[0].name =
            "當前電流值";
          vm.smartMeterChart.options[0].value.data.emeter_data[1].value =
            result["sensor"]["emeter_data"]["voltage"]["value"];
          vm.smartMeterChart.options[0].value.data.emeter_data[1].unit =
            result["sensor"]["emeter_data"]["voltage"]["unit"];
          vm.smartMeterChart.options[0].value.data.emeter_data[1].name =
            "當前電壓";
          vm.smartMeterChart.options[0].value.data.emeter_data[2].value =
            result["sensor"]["emeter_data"]["volt_vubr"]["value"];
          vm.smartMeterChart.options[0].value.data.emeter_data[2].unit =
            result["sensor"]["emeter_data"]["volt_vubr"]["unit"];
          vm.smartMeterChart.options[0].value.data.emeter_data[2].name =
            "電壓不平衡率";
          vm.relec.table.items = [
            {
              value:
