    _onResize_elecboard() {
      let vm = this;
      vm.window_innerWidth = window.innerWidth;
      vm._onResize_elecboard_detail_list();
      setTimeout(() => {
        vm._onResize_elecboard_status_bar();
      }, 0);
    },

    _onResize_elecboard_detail_list() {
      let vm = this;
      if (vm.nowPage == "elecboard") {
        let show_device_list = [];
        if (vm.window_innerWidth > 992) {
          let divisor = Math.round(vm.device_array.length / 2);
          vm.device_array.forEach((data, index) => {
            if (index < divisor) {
              show_device_list[index * 2] = data;
            } else {
              show_device_list[(index - divisor) * 2 + 1] = data;
            }
          });
        } else {
          show_device_list = vm.device_array;
        }
        vm.show_device_list = show_device_list;
      }
    },

    _onResize_elecboard_status_bar() {
      let vm = this;
      if (vm.nowPage == "elecboard") {
        vm.elecboard_detail_status_height =
          vm.$refs.elecboard_detail.clientHeight -
          vm.$refs.elecboard_status_bar.clientHeight -
          16;
      }
    },

    //資料reset
    _reset(keep) {
      var def = this.$options.data();
      for (var i = 0; i < keep.length; i++) {
        def[keep[i]] = this[keep[i]];
      }
      Object.assign(this.$data, def);
      //https://codepen.io/karimcossutti/pen/ObXyKq
    },
  },
  destroyed() {
    this._clearTimer();
  },
};
</script>

