import axios from "axios";
import { mergeWith } from "lodash";

const https = require("https");
//https://github.com/axios/axios/issues/535
const api = axios.create({
    // baseURL: "https://192.168.88.71:3687/"
    // baseURL: "https://59.125.151.182:3687/"
    baseURL: "https://" + location.hostname + ":3687/"
})
const api_uid = { uid: "@sapido@PaaS" }

function customizer(objValue, srcValue) {
    if (_.isArray(objValue)) {
        return objValue.concat(srcValue);
    }
}

const state = {
    axiosResult: null
};
const getters = {
    get_axiosResult: state => {
        return state.axiosResult;
    }
};
const actions = {
    async axiosAction(context, params) {
        await axios
            .post("/php/modules.php", params)
            .then(
                function (response) {
                    const result = response.data;
                    context.commit("AXIOSACTION", result);
                }.bind(this)
            )
            .catch(function (err) {
                console.log(err);
            });
    },
    async axiosGetAction(context, params) {
        console.log("axiosGetAction");
        let url_params = {
            params: {}
        };
        if (params.hasOwnProperty("urlparams")) {
            url_params.params = params.urlparams
        }

        mergeWith(
            url_params.params,
            api_uid,
            customizer
        );
        console.log(url_params);
        await api
            .get(params.url, url_params, {
                httpsAgent: new https.Agent({
                    rejectUnauthorized: false
                })
            })
            .then(
                function (response) {
                    console.log(response);
                    const result = response.data;
                    context.commit("AXIOSACTION", result);
                }.bind(this)
            )
            .catch(function (err) {
                state.axiosResult = null;
                console.log(err);
            });
    },
    async axiosPostAction(context, params) {
        console.log("axiosPostAction");
        await api
            .post(params.url, params.postdata, {
                httpsAgent: new require("https").Agent({
                    rejectUnauthorized: false
                })
            })
            .then(
                function (response) {
                    console.log(response);
                    const result = response.data;
                    context.commit("AXIOSACTION", result);
                }.bind(this)
            )
            .catch(function (err) {
                state.axiosResult = null;
                console.log(err);
            });
    },
    async axiosPatchAction(context, params) {
        console.log("axiosPatchAction");
        await api
            .patch(params.url, params.postdata, {
                httpsAgent: new require("https").Agent({
                    rejectUnauthorized: false
                })
            })
            .then(
                function (response) {
                    console.log(response);
                    const result = response.data;
                    context.commit("AXIOSACTION", result);
                }.bind(this)
            )
            .catch(function (err) {
                state.axiosResult = null;
                console.log(err);
            });
    },
    async axiosDeleteAction(context, params) {
        console.log("axiosDeleteAction");
        await api
            .delete(params.url, params.postdata, {
                httpsAgent: new require("https").Agent({
                    rejectUnauthorized: false
                })
            })
            .then(
                function (response) {
                    console.log(response);
                    const result = response.data;
                    context.commit("AXIOSACTION", result);
                }.bind(this)
            )
            .catch(function (err) {
                state.axiosResult = null;
                console.log(err);
            });
    },
};
const mutations = {
    AXIOSACTION(state, result) {
        state.axiosResult = result;
    }
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
};
