<template>
  <b-button
    class="exportCsv"
    variant="primary"
    style="
      background-color: #536172;
      border-color: #536172;
      width: 5rem;
      height: 1.375rem;
      padding: 0 0.5rem;
      font-family: 'dfyuanstd-w7';
      font-size: 0.875rem;
      font-weight: normal;
    "
    @click="_exportCsv()"
    >匯出Excel</b-button
  >
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "exportCsv",
  data() {
    return {};
  },
  created: function () {},
  components: {},
  computed: {
    ...mapGetters({
      axiosResult: "mescommonaxios/get_axiosResult",

      exportData: "mesExportCsv/get_exportData",
      exportDataTitle: "mesExportCsv/get_exportDataTitle",
      exportFileName: "mesExportCsv/get_exportFileName",
    }),
  },
  watch: {},
  methods: {
    ...mapActions({
      axiosAction: "mescommonaxios/axiosAction",
      setSetTimeOutAlertMsg: "alertmodal/set_setTimeOutAlertMsg",
      toggleSetTimeOutAlertModal: "alertmodal/toggle_settimeout_alertModal",
    }),

    _exportCsv() {
      let vm = this;
      let exportData = vm.exportData;
      let exportDataTitle = vm.exportDataTitle;
      let exportFileName = vm.exportFileName;
      if (exportDataTitle.length == 0 || exportFileName == "") {
        vm.setSetTimeOutAlertMsg("無匯出資料");
        vm.toggleSetTimeOutAlertModal();
        return;
      }
      // let exportData = exportData;
      const data = exportData.map((itemsdata) => Object.values(itemsdata));
      let exportfilename = exportFileName;
      //console.log(data);
      //加入空白，匯出Excel不會改變格式
      data.forEach((item) => {
        item[0] = " " + item[0];
      });
      //加表頭
      data.unshift(exportDataTitle);

      const csvContent = data.map((row) => row.join(",")).join("\n");
      const link = document.createElement("a");
      link.setAttribute(
        "href",
        "data:text/csv;charset=utf-8,%EF%BB%BF" + encodeURI(csvContent)
      );
      link.style = "visibility:hidden";
      link.download = exportfilename + ".csv";
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    },
  },
};
</script>

