<template>
  <div id="GeneralMach">
    <b-row>
      <b-col cols="12" class="text-right my-3" style="font-size: 1rem">
        <span class="my-0" id="elecboard-update-time">更新時間:</span>
        <span class="my-0" for="elecboard-update-time">{{ nowtime }}</span>
      </b-col>
      <b-col :cols="window_innerWidth > 992 ? 1 : 0" class="px-0"></b-col>
      <b-col :cols="window_innerWidth > 992 ? 11 : 12" class="px-0"></b-col>
      <b-col cols="1"> </b-col>
      <b-col cols="10">
        <b-row class="mx-0" style="font-family: dfyuanstd-w8, dfyuanstd-w7">
          <b-col
            cols="12"
            class="text-left pb-2"
            :class="{ 'px-0': window_innerWidth > 992 }"
            style="
              color: #a3bedb;
              font-size: 1.75rem;
              font-family: dfyuanstd-w8, dfyuanstd-w7;
            "
          >
            平面總覽
          </b-col>
          <b-col cols="11" class="mx-0" style="padding: 0px">
            <b-row class="mx-0">
              <b-col
                v-for="(device_item, index) in showDeviceData"
                :key="index"
                cols="6"
                xl="3"
                lg="3"
                md="6"
                sm="6"
                style="
                  padding-top: 15px;
                  padding-bottom: 15px;
                "
                :class="{ 'px-0': window_innerWidth > 992 }"
              >
                <b-row>
                  <b-col
                    cols="12"
                    xl="12"
                    lg="12"
                    md="12"
                    sm="12"
                    class="px-3 text-center"
                    style="align-self: center"
                  >
                    <div>
                      <div
                        class="m-0"
                        style="
                          background-color: #cccccc;
                          color: #ffffff;
                          line-height: 30.5px;
                          padding-top:15px;
                          padding-bottom:15px;
                          margin-top:10px;
                          margin-bottom:10px;
                        "
                        :style="{
                          'background-color':
                            device_item.machine_status == '運作'
                              ? '#00b152'
                              : device_item.machine_status == '待機'
                              ? '#ffc100'
                              : device_item.machine_status == '異常'
                              ? '#c10000'
                              : '#cccccc',
                            
                          'cursor': device_item.linkTo ?  'pointer':'auto',
                        }"
                        @click="_viewDeviceDetail(device_item.device_name,index)"
                      >
                      
                      <div>{{ device_item.device_name }}</div>                         
                      <div>型號 : {{device_item.type}}</div>
                      <div>狀態 : {{device_item.machine_status}}</div>
                
                    </div>
                  </b-col>
                </b-row>
              </b-col>
            </b-row>
          </b-col>
          <b-col cols="1"> </b-col>
        </b-row>
      </b-col>
      <b-col cols=""> </b-col>
    </b-row>
  </div>
</template>

<script>
import axios from "axios";
import { mapGetters, mapActions } from "vuex";
import alertModal from "@/components/MES/alertModal.vue";
import commonQueryIoT from "@/components/MES/commonQueryIoT.vue";
import queryResult from "@/components/MES/queryResult.vue";
export default {
  name: "GneralMach",
  data() {
    let vm = this;
    return {
      nowPage: "gneralMach",
      process_name: "",
      nowtime: "",
      device_data: [],
      device_array: [],
      totalDeviceStatus: {
        離線: 0,
        異常: 0,
        待機: 0,
        運作: 0,
      },
      showDeviceData: [
        {
          type: "CBP136L",
          device_name: "F01",
          machine_status: "運作",
          linkTo:false
        },
        {
          type: "-",
          device_name: "-",
          machine_status: "離線",
          linkTo:false
        },
        {
          type: "-",
          device_name: "-",
          machine_status: "離線",
          linkTo:false
        },
        {
          type: "-",
          device_name: "-",
          machine_status: "離線",
          linkTo:false
        },
        {
          type: "-",
          device_name: "-",
          machine_status: "離線",
          linkTo:false
        },
      ],
    };
  },
  created() {
    let vm = this;
    vm.changeSelectPage("generalmach");
    vm.device_array = ["F01", "F02", "F03", "F04", "F05"];
    vm._getNowTime();
    vm._getMachineData();

    // this.changeSelectPageName(this.pageName.devicesetting);
    // this._setModelOption();
    // this._setDeviceOption();
    // this._setCommonQueryData();
    // this._setQueryInput();
  },
  mounted: function () {
    let vm = this;
  },
  computed: {
    ...mapGetters({
      axiosResult: "mescommonaxios/get_axiosResult",
      selectPage: "mesGetLogin/get_selectPage",
      pageName: "mesGetLogin/get_pageName",
      mainQueryResponse: "mesCommonQueryIoT/get_queryResponse",
      clickChild: "featurelistbar/get_clickChild",
    }),
  },
  components: {
    alertModal,
    queryResult,
    commonQueryIoT,
  },
  watch: {},
  methods: {
    ...mapActions({
      axiosAction: "mescommonaxios/axiosAction",
      changeSelectPage: "mesGetLogin/change_selectPage",
      changeSelectPageName: "mesGetLogin/change_selectPageName",
      setPhpFunction: "mesCommonQueryIoT/set_phpfunction",
      setSelectTag: "mesCommonQueryIoT/set_selectTag",
      setModelOption: "mesCommonQueryIoT/set_modelOption",
      setDeviceOption: "mesCommonQueryIoT/set_deviceOption",
      setQueryResultValue: "mesQueryResult/set_queryResultItem",
      clearQueryResultValue: "mesQueryResult/clear_queryResultItem",
    }),

    _setCommonQueryData() {
      let vm = this;
      vm.setPhpFunction("MainQuery");
    },

    _setQueryInput() {
      let vm = this;
      vm.setSelectTag({
        startTime: false,
        endTime: false,
        deviceCode: true,
      });
    },

    _setModelOption() {
      let vm = this;
      //   vm.setModelOption([{ value: "CBP136L", text: "CBP136L" }]);
    },

    _setDeviceOption() {
      let vm = this;
      vm.setDeviceOption({
        CBP136L: [{ value: "F01", text: "F01" }],
      });
    },
    _getMachineData() {
      let vm = this;
      var params = {};
      params["vuePage"] = vm.selectPage;
      params["methods"] = "GET";
      params["whichFunction"] = "MainQuery";
      vm.query_time = new Date();
      vm.axiosAction(params).then(() => {
        vm.searching = false;
        var result = vm.axiosResult;
        if (result.Response == "ok") {
          let result_data = result["QueryTableData"];
          vm.device_data[result_data[0]["device_name"]] = result_data[0];
          vm._setDeviceShow();
        }
      });
      if (vm.query_time != null) {
        vm.query_time = null;
        setTimeout(function () {
          vm._getMachineData();
        }, 1000);
      }
      vm._getNowTime();
    },
    _setDeviceShow() {
      let vm = this;
      vm.showDeviceData.forEach((item, index) => {
        if (vm.device_data[item.device_name]) {
          vm.showDeviceData[index].device_name =
            vm.device_data[item.device_name].device_name;
          vm.showDeviceData[index].machine_status =
            vm.device_data[item.device_name].machine_status;
            vm.device_data[item.device_name].device_name;
          vm.showDeviceData[index].linkTo = true;
        }
      });
    },
    _clearTimer() {
      var highestIntervalId = setInterval(";");
      for (var i = 0; i < highestIntervalId; i++) {
        //清除所有計時資料
        clearInterval(i);
      }
      if (this.selectPage == "elecboard") {
        this._getNowTime();
      }
    },
    _getNowTime() {
      let vm = this;
      let Today = new Date();
      vm.nowtime =
        Today.getFullYear() +
        "-" +
        String(Today.getMonth() + 1).padStart(2, "0") +
        "-" +
        String(Today.getDate()).padStart(2, "0") +
        " " +
        String(Today.getHours()).padStart(2, "0") +
        ":" +
        String(Today.getMinutes()).padStart(2, "0") +
        ":" +
        String(Today.getSeconds()).padStart(2, "0");
    },
    _viewDeviceDetail(device_name,index) {
      let vm = this;
      if(vm.showDeviceData[index].linkTo){
      vm.nowPage = "machstatus";
      vm.changeSelectPage("elecboard");
      // vm.changeSelectPageName(this.pageName.elecboard);
      vm.$router.push({
        // path:"/" + vm.$route.params.SYSTEM + "/mes/elecboard",
        name: "elecBoard",
        params: { device_data: device_name, nowPage: "machstatus" },
      });
      vm._clearTimer();
      }
    },
  },
  destroyed() {
    this._clearTimer();
  },
};
</script>

