<template>
  <div id="Activation">
    <b-row class="mx-0 mb-3">
      <b-col cols="1"> </b-col>

      <b-col cols="10">
        <b-col
          cols="12"
          class="p-0 m-0 mt-4 text-center"
          style="font-size: 1.75rem; color: #000000"
        >
          <span class="m-auto">稼動率</span>
        </b-col>

        <b-col cols="12" class="mx-0 px-0" style="margin-top: 2rem">
          <commonQueryIoT />
        </b-col>

        <b-col cols="12" class="px-0" style="margin-top: 2rem">
          <queryResult />
        </b-col>

        <singleChart class="text-left" style="margin-top: 2rem" />

        <b-col cols="12" class="text-right px-0">
          <exportCsv />
        </b-col>

        <b-col
          cols="12"
          class="text-right mx-0 px-0"
          style="margin-top: 0.6rem"
        >
          <activationData class="text-left" />
        </b-col>
      </b-col>

      <b-col cols="1"> </b-col>
    </b-row>
  </div>
</template>

<script>
import axios from "axios";
import { mapGetters, mapActions } from "vuex";
import alertModal from "@/components/MES/alertModal.vue";
import chart from "@/components/MES/chart.vue";
import commonQueryIoT from "@/components/MES/commonQueryIoT.vue";
import queryResult from "@/components/MES/queryResult.vue";
import singleChart from "@/components/MES/singleChart.vue";
import exportCsv from "@/components/MES/exportCsv.vue";
import activationData from "@/components/MES/activationData.vue";
export default {
  name: "Activation",
  data() {
    let vm = this;
    return {
      query_date: "",
      device_name: "",
      device_option: {
        打頭: ["F07", "F12"],
        輾牙: ["R09", "F12"],
      },
      activationChart: {
        name: {
          chartId: "activationChart",
          chartName: "警報履歷圖表",
        },
        options: [
          {
            text: "依日統計",
            value: {
              name: "day",
              data: {
                senTime: [],
                senData: {
                  bar: {},
                  line: {
                    name: "平均稼動率",
                    data: [],
                    color: "#000000",
                  },
                  unit: "%",
                },
                senType: "VerticalBarLine",
              },
            },
          },
          {
            text: "依周統計",
            value: {
              name: "week",
              data: {
                senTime: [],
                senData: {
                  bar: {},
                  line: {
                    name: "平均稼動率",
                    data: [],
                    color: "#000000",
                  },
                  unit: "%",
                },
                senType: "VerticalBarLine",
              },
            },
          },
          {
            text: "依月統計",
            value: {
              name: "month",
              data: {
                senTime: [],
                senData: {
                  bar: {},
                  line: {
                    name: "平均稼動率",
                    data: [],
                    color: "#000000",
                  },
                  unit: "%",
                },
                senType: "VerticalBarLine",
              },
            },
          },
          // {
          //   text: "依年統計",
          //   value: {
          //     name: "year",
          //     data: {
          //       senTime: [],
          //       senData: {
          //         bar: {},
          //         line: {
          //           name: "平均稼動率",
          //           data: [],
          //           color: "#000000",
          //         },
          //         unit: "%",
          //       },
          //       senType: "VerticalBarLine",
          //     },
          //   },
          // },
        ],
      },
    };
  },
  created() {
    this.changeSelectPage("activation");
    this._setCommonQueryData();
    this._setQueryInput();
    this._setModelOption();
    this._setDeviceOption();
    this.setSingleChartName(this.activationChart.name);
    this.setSingleChartSelected(this.activationChart.options[0].value);
    this.setSingleChartOptions(this.activationChart.options);
  },
  mounted() {
    let vm = this;
    vm._resetExportCsv();
    vm._setExportDataTitle();
  },
  computed: {
    ...mapGetters({
      pageName: "mesGetLogin/get_pageName",
      mainQueryResponse: "mesCommonQueryIoT/get_queryResponse",
      activationDataFields: "mesActivationData/get_activationDataFields",
    }),
  },
  components: {
    alertModal,
    chart,
    commonQueryIoT,
    queryResult,
    singleChart,
    exportCsv,
    activationData,
  },
  watch: {
    mainQueryResponse: {
      handler() {
        let vm = this;
        vm._reset([]);
        if (
          vm.mainQueryResponse == "查無資料" ||
          vm.mainQueryResponse == "時間尚未選擇"
        ) {
          vm.selected = vm.options[0].value;
          return;
        }

        var itemsArray = [];
        var devicearray = [];
        if (vm.mainQueryResponse[0].device_detail_data.length > 0) {
          for (
            var i = 0;
            i < vm.mainQueryResponse[0].device_detail_data.length;
            i++
          ) {
            var itemsObj = {};
            itemsObj["date"] =
              vm.mainQueryResponse[0].device_detail_data[i]["date"];
            itemsObj["activation"] =
              vm.mainQueryResponse[0].device_detail_data[i]["activation"];
            itemsArray.push(itemsObj);
          }
        }
        console.log(itemsArray);
        vm.activationChart.options[0].value.data.senTime =
          vm.mainQueryResponse[0].device_data_day.data_time;
        vm.activationChart.options[0].value.data.senData.bar =
          vm.mainQueryResponse[0].device_data_day.data;

        vm.activationChart.options[1].value.data.senTime =
          vm.mainQueryResponse[0].device_data_month.data_time;
        vm.activationChart.options[1].value.data.senData.bar =
          vm.mainQueryResponse[0].device_data_month.data;

        vm.activationChart.options[2].value.data.senTime =
          vm.mainQueryResponse[0].device_data_year.data_time;
        vm.activationChart.options[2].value.data.senData.bar =
          vm.mainQueryResponse[0].device_data_year.data;

        vm.activationChart.selected = vm.activationChart.options[0].value;
        vm.setSingleChartSelected(vm.activationChart.options[0].value);
        vm.setSingleChartOptions(vm.activationChart.options);
        vm.query_date =
          vm.mainQueryResponse[0].query_date[0].split(" ")[0] +
          "~" +
          vm.mainQueryResponse[0].query_date[1].split(" ")[0];

        vm.setQueryResultValue([
          {
            date: vm.query_date,
            model: vm.mainQueryResponse[0].query_date[2],
            device: vm.mainQueryResponse[0].query_date[3],
          },
        ]);
        vm.setSingleChartName(vm.activationChart.name);
        vm.setSingleChartSelected(vm.activationChart.options[0].value);
        vm.setSingleChartOptions(vm.activationChart.options);
        vm.setActivationDataItem(itemsArray);
        vm.setExportData(itemsArray);
      },
    },
  },
  methods: {
    ...mapActions({
      changeSelectPage: "mesGetLogin/change_selectPage",
      setPhpFunction: "mesCommonQueryIoT/set_phpfunction",
      setSelectTag: "mesCommonQueryIoT/set_selectTag",
      setModelOption: "mesCommonQueryIoT/set_modelOption",
      setDeviceOption: "mesCommonQueryIoT/set_deviceOption",
      setQueryResultValue: "mesQueryResult/set_queryResultItem",
      setSingleChartSelected: "mesSingleChart/set_singleChartSelected",
      setSingleChartOptions: "mesSingleChart/set_singleChartOptions",
      setSingleChartName: "mesSingleChart/set_singleChartName",
      setExportData: "mesExportCsv/set_exportData",
      setExportDataTitle: "mesExportCsv/set_exportDataTitle",
      setExportFileName: "mesExportCsv/set_exportFileName",
      setActivationDataItem: "mesActivationData/set_activationDataItem",
    }),

    _setCommonQueryData() {
      let vm = this;
      vm.setPhpFunction("MainQuery");
    },

    _setQueryInput() {
      let vm = this;
      vm.setSelectTag({
        model: true,
        deviceCode: true,
      });
    },

    _setModelOption() {
      let vm = this;
      vm.setModelOption([{ value: "CBP136L", text: "CBP136L" }]);
    },

    _setDeviceOption() {
      let vm = this;
      vm.setDeviceOption({
        CBP136L: [{ value: "F01", text: "F01" }],
      });
    },

    _resetExportCsv() {
      let vm = this;
      vm.setExportData([]);
      vm.setExportDataTitle([]);
      vm.setExportFileName("");
    },

    _setExportDataTitle() {
      let vm = this;
      let exportDataTitle = [];
      vm.activationDataFields.forEach((data) => {
        exportDataTitle.push(data.label);
      });
      vm.setExportDataTitle(exportDataTitle);
    },

    //資料reset
    _reset(keep) {
      var def = this.$options.data();
      for (var i = 0; i < keep.length; i++) {
        def[keep[i]] = this[keep[i]];
      }
      Object.assign(this.$data, def);
      //https://codepen.io/karimcossutti/pen/ObXyKq
    },
  },
};
</script>

