<template>
  <div id="AlarmHistory">
    <b-row class="mx-0">
      <b-col cols="1"> </b-col>

      <b-col cols="10">
        <b-col
          cols="12"
          class="p-0 m-0 mt-4 text-center"
          style="font-size: 1.75rem; color: #000000"
        >
          <span class="m-auto">警報履歷</span>
        </b-col>

        <b-col cols="12" class="mx-0 px-0" style="margin-top: 2rem">
          <!-- <b-row> -->
          <commonQueryIoT />
          <!-- </b-row> -->
        </b-col>

        <b-col cols="12" class="mx-0 px-0" style="margin-top: 2rem">
          <queryResult />
        </b-col>

        <singleChart class="text-left" style="margin-top: 2rem" />

        <b-col cols="12" class="text-right px-0">
          <exportCsv />
        </b-col>

        <b-col
          cols="12"
          class="text-right mx-0 px-0"
          style="margin-top: 0.6rem"
        >
          <alarmHistoryData class="text-left" />
        </b-col>
      </b-col>

      <b-col cols="1"> </b-col>
    </b-row>
  </div>
</template>

<script>
import axios from "axios";
import { mapGetters, mapActions } from "vuex";
import alertModal from "@/components/MES/alertModal.vue";
import chart from "@/components/MES/chart.vue";
import commonQueryIoT from "@/components/MES/commonQueryIoT.vue";
import queryResult from "@/components/MES/queryResult.vue";
import singleChart from "@/components/MES/singleChart.vue";
import exportCsv from "@/components/MES/exportCsv.vue";
import alarmHistoryData from "@/components/MES/alarmHistoryData.vue";
export default {
  name: "AlarmHistory",
  data() {
    let vm = this;
    return {
      slidebar_right: true,
      query_date: "",
      device_name: "",

      alarmHistoryChart: {
        name: {
          chartId: "alarmHistoryChart",
          chartName: "警報履歷圖表",
        },
        options: [
          {
            text: "依警報類型",
            value: {
              name: "alarmHistoryAlarmChart-abn",
              data: {
                senTime: [],
                senData: {
                  bar: {},
                  line: {
                    name: "平均警報次數",
                    data: [],
                    color: "#FF3300",
                  },
                  unit: "次數",
                },
                senType: "VerticalBarLine",
              },
            },
          },
        ],
      },
    };
  },
  created() {
    this.changeSelectPage("alarmhistory");
    // this.changeSelectPageName(this.pageName.alarmhistory);
    this._setCommonQueryData();
    this._setQueryInput();
    this._setModelOption();
    this._setDeviceOption();
    this.setSingleChartName(this.alarmHistoryChart.name);
    this.setSingleChartSelected(this.alarmHistoryChart.options[0].value);
    this.setSingleChartOptions(this.alarmHistoryChart.options);
  },
  mounted() {
    let vm = this;
    vm._resetExportCsv();
    vm._setExportDataTitle();
  },
  watch: {
    mainQueryResponse: {
      handler() {
        let vm = this;
        vm._reset([]);
        if (
          vm.mainQueryResponse == "查無資料" ||
          vm.mainQueryResponse == "時間尚未選擇"
        ) {
          vm.selected = vm.options[0].value;
          return;
        }
        var itemsarray = [];
        var devicearray = [];
        if (vm.mainQueryResponse[0].device_detail_data.length >= 0) {
          for (
            var i = 0;
            i < vm.mainQueryResponse[0].device_detail_data.length;
            i++
          ) {
            var itemsobj = {};
            itemsobj["alarmCode"] =
              vm.mainQueryResponse[0].device_detail_data[i]["alarmCode"];
            itemsobj["alarmDetail"] =
              vm.mainQueryResponse[0].device_detail_data[i]["alarmDetail"];
            itemsobj["alarmVideo"] =
              vm.mainQueryResponse[0].device_detail_data[i]["alarmVideo"];
            itemsobj["startTime"] =
              vm.mainQueryResponse[0].device_detail_data[i]["startTime"];
            itemsobj["endTime"] =
              vm.mainQueryResponse[0].device_detail_data[i]["endTime"];
            itemsobj["continuousTime"] =
              vm.mainQueryResponse[0].device_detail_data[i]["continuousTime"];
            itemsarray.push(itemsobj);
          }

          console.log(itemsarray);
          vm.alarmHistoryChart.options[0].value.data.senTime =
            vm.mainQueryResponse[0].abn_species.data_time;
          vm.alarmHistoryChart.options[0].value.data.senData.bar =
            vm.mainQueryResponse[0].abn_species.data;

          vm.selected = vm.alarmHistoryChart.options[0].value;
          vm.query_date =
            vm.mainQueryResponse[0].query_date[0].split(" ")[0] +
            "~" +
            vm.mainQueryResponse[0].query_date[1].split(" ")[0];

          vm.setQueryResultValue([
            {
              date: vm.query_date,
              model: vm.mainQueryResponse[0].query_date[2],
              device: vm.mainQueryResponse[0].query_date[3],
            },
          ]);
          vm.setSingleChartName(vm.alarmHistoryChart.name);
          vm.setSingleChartSelected(vm.alarmHistoryChart.options[0].value);
          vm.setSingleChartOptions(vm.alarmHistoryChart.options);
          vm.setAlarmHistoryDataItem(itemsarray);
          // vm.set_deviceName(vm.mainQueryResponse[0].query_date[5]);
          vm.setExportData(itemsarray);
        }
      },
    },
  },
  computed: {
    ...mapGetters({
      pageName: "mesGetLogin/get_pageName",
      mainQueryResponse: "mesCommonQueryIoT/get_queryResponse",
      alarmHistoryDataFields: "mesAlarmHistoryData/get_alarmHistoryDataFields",
    }),
  },
  components: {
    alertModal,
    chart,
    commonQueryIoT,
    queryResult,
    singleChart,
    exportCsv,
    alarmHistoryData,
  },
  methods: {
    ...mapActions({
      setSetTimeOutAlertMsg: "alertmodal/set_setTimeOutAlertMsg",
      toggleSetTimeOutAlertModal: "alertmodal/toggle_settimeout_alertModal",
      changeSelectPage: "mesGetLogin/change_selectPage",
      changeSelectPageName: "mesGetLogin/change_selectPageName",
      setPhpFunction: "mesCommonQueryIoT/set_phpfunction",
      setSelectTag: "mesCommonQueryIoT/set_selectTag",
      setModelOption: "mesCommonQueryIoT/set_modelOption",
      setDeviceOption: "mesCommonQueryIoT/set_deviceOption",
      setQueryResultValue: "mesQueryResult/set_queryResultItem",
      clearQueryResultValue: "mesQueryResult/clear_queryResultItem",
      setSingleChartSelected: "mesSingleChart/set_singleChartSelected",
      setSingleChartOptions: "mesSingleChart/set_singleChartOptions",
      setSingleChartName: "mesSingleChart/set_singleChartName",
      setExportData: "mesExportCsv/set_exportData",
      setExportDataTitle: "mesExportCsv/set_exportDataTitle",
      setExportFileName: "mesExportCsv/set_exportFileName",
      setAlarmHistoryDataItem: "mesAlarmHistoryData/set_alarmHistoryDataItem",
      clearAlarmHistoryDataItem:
        "mesAlarmHistoryData/clear_alarmHistoryDataItem",
    }),

    _setCommonQueryData() {
      let vm = this;
      vm.setPhpFunction("MainQuery");
    },

    _setQueryInput() {
      let vm = this;
      vm.setSelectTag({
        model: true,
        deviceCode: true,
      });
    },

    _setModelOption() {
      let vm = this;
      vm.setModelOption([{ value: "CBP136L", text: "CBP136L" }]);
    },

    _setDeviceOption() {
      let vm = this;
      vm.setDeviceOption({
        CBP136L: [{ value: "F01", text: "F01" }],
      });
    },

    _resetExportCsv() {
      let vm = this;
      vm.setExportData([]);
      vm.setExportDataTitle([]);
      vm.setExportFileName("");
    },

    _setExportDataTitle() {
      let vm = this;
      let exportDataTitle = [];
      vm.alarmHistoryDataFields.forEach((data) => {
        exportDataTitle.push(data.label);
      });
      vm.setExportDataTitle(exportDataTitle);
    },

    //資料reset
    _reset(keep) {
      var def = this.$options.data();
      for (var i = 0; i < keep.length; i++) {
        def[keep[i]] = this[keep[i]];
      }
      Object.assign(this.$data, def);
      //https://codepen.io/karimcossutti/pen/ObXyKq
    },
  },
  destroyed() {
    this.clearQueryResultValue();
    this.clearAlarmHistoryDataItem();
    // this.clearTimer();
  },
};
</script>

