<template>
  <div id="OperateLog">
    <b-row class="mx-0 mb-3">
      <b-col cols="1"> </b-col>

      <b-col cols="10">
        <b-col
          cols="12"
          class="p-0 m-0 mt-4 text-center"
          style="font-size: 1.75rem; color: #000000"
        >
          <span class="m-auto">運轉日誌</span>
        </b-col>

        <b-col cols="12" class="mx-0 px-0" style="margin-top: 2rem">
          <b-row>
            <commonQueryIoT />
          </b-row>
        </b-col>

        <b-col cols="12" style="margin-top: 2rem" class="px-0">
          <queryResult />
        </b-col>

        <!-- <stackBar style="margin-top: 2rem; padding: 0px 15px" /> -->
        <stackBar
          :stacksBarClass="stacksBarClass"
          :stacksBarMaxValue="stacksBarMaxValue"
          :stacksData="stacksBar_data != '' ? stacksBar_data : stacksBarDefault"
          :stacksSize="'normal'"
        />

        <!-- <singleChart class="text-left" style="margin-top: 2rem" /> -->

        <b-col cols="12" class="text-right px-0">
          <exportCsv />
        </b-col>

        <b-col
          cols="12"
          class="text-right mx-0 px-0"
          style="margin-top: 0.6rem"
        >
          <operateLogData class="text-left" />
        </b-col>
      </b-col>

      <b-col cols="1"> </b-col>
    </b-row>
  </div>
</template>

<script>
import axios from "axios";
import { mapGetters, mapActions } from "vuex";
import alertModal from "@/components/MES/alertModal.vue";
import stackBar from "@/components/MES/stackBar.vue";
import commonQueryIoT from "@/components/MES/commonQueryIoT.vue";
import queryResult from "@/components/MES/queryResult.vue";
import exportCsv from "@/components/MES/exportCsv.vue";
import operateLogData from "@/components/MES/operateLogData.vue";
export default {
  name: "OperateLog",
  data() {
    let vm = this;
    return {
      stacksBar_data: "",
      stacksBarDefault: {
        default: [
          {
            status: null,
            endTime: "2021-04-08 08:00:29",
            alarmDetail: "",
            startTime: "2021-04-08 08:00:00",
            duration_number: 0,
          },
        ],
      },
    };
  },
  created() {
    this.changeSelectPage("operatelog");
    // this.changeSelectPageName(this.pageName.operatelog);
    this._setModelOption();
    this._setDeviceOption();
    this._setCommonQueryData();
    this._setQueryInput();
  },
  mounted: function () {
    let vm = this;
    vm._resetExportCsv();
    vm._setExportDataTitle();
  },
  computed: {
    ...mapGetters({
      pageName: "mesGetLogin/get_pageName",
      mainQueryResponse: "mesCommonQueryIoT/get_queryResponse",
      operateLogDataFields: "mesOperateLogData/get_operateLogDataFields",
      stacksBarClass: "mesStackBar/get_stacksBarClass",
      stacksBarMaxValue: "mesStackBar/get_stacksBarMaxValue",
    }),
  },
  components: {
    alertModal,
    stackBar,
    queryResult,
    operateLogData,
    commonQueryIoT,
    exportCsv,
  },
  watch: {
    mainQueryResponse: {
      handler() {
        let vm = this;
        if (
          vm.mainQueryResponse == "查無資料" ||
          vm.mainQueryResponse == "時間尚未選擇"
        ) {
          vm.clearOperateLogDataItem();
          vm.setExportData([]);
          vm.clearStacksData();
          return;
        }
        var itemsarray = [];
        let test = 0;
        for (var i = 0; i < vm.mainQueryResponse.length; i++) {
          for (var j = 0; j < vm.mainQueryResponse[i].total_data.length; j++) {
            let total_data = vm.mainQueryResponse[i].total_data;
            var itemsobj = {};
            itemsobj["status"] = total_data[j]["status"];
            itemsobj["alarmCode"] = total_data[j]["alarmCode"];
            itemsobj["alarmDetail"] = total_data[j]["alarmDetail"];
            itemsobj["startTime"] = total_data[j]["startTime"];
            itemsobj["endTime"] = total_data[j]["endTime"];
            itemsobj["durationTime"] = total_data[j]["durationTime"];
            // itemsobj["device_name"] = total_data[j]["device_name"];
            // itemsobj["place_item"] = total_data[j]["place_item"];
            // itemsobj["group_item"] = total_data[j]["group_item"];
            // itemsobj["class_item"] = total_data[j]["class_item"];
            itemsarray.push(itemsobj);
          }
          console.log(itemsarray);
          vm.setExportData(itemsarray);
          vm.setOperateLogDataItem(itemsarray);

          //堆疊圖
          vm.setStacksData(vm.mainQueryResponse[i].machine_data);
          vm.stacksBar_data = vm.mainQueryResponse[i].machine_data;
          console.log(vm.mainQueryResponse[0].query_date);
          vm.setQueryResultValue([
            {
              date: vm.mainQueryResponse[0].query_date[0].split(" ")[0],
              model: vm.mainQueryResponse[0].query_date[1],
              device: vm.mainQueryResponse[0].query_date[2],
            },
          ]);
        }
      },
    },
  },
  methods: {
    ...mapActions({
      changeSelectPage: "mesGetLogin/change_selectPage",
      changeSelectPageName: "mesGetLogin/change_selectPageName",
      setPhpFunction: "mesCommonQueryIoT/set_phpfunction",
      setSelectTag: "mesCommonQueryIoT/set_selectTag",
      setModelOption: "mesCommonQueryIoT/set_modelOption",
      setDeviceOption: "mesCommonQueryIoT/set_deviceOption",
      setQueryResultValue: "mesQueryResult/set_queryResultItem",
      clearQueryResultValue: "mesQueryResult/clear_queryResultItem",
      setOperateLogDataItem: "mesOperateLogData/set_operateLogDataItem",
      clearOperateLogDataItem: "mesOperateLogData/clear_operateLogDataItem",
      setExportData: "mesExportCsv/set_exportData",
      setExportDataTitle: "mesExportCsv/set_exportDataTitle",
      setExportFileName: "mesExportCsv/set_exportFileName",
      setStacksData: "mesStackBar/set_stacksData",
      clearStacksData: "mesStackBar/clear_stacksData",
    }),

    _setCommonQueryData() {
      let vm = this;
      vm.setPhpFunction("MainQuery");
    },

    _setQueryInput() {
      let vm = this;
      vm.setSelectTag({
        endTime: false,
        deviceCode: true,
        model: true,
      });
    },

    _setModelOption() {
      let vm = this;
      vm.setModelOption([{ value: "CBP136L", text: "CBP136L" }]);
    },

    _setDeviceOption() {
      let vm = this;
      vm.setDeviceOption({
        CBP136L: [{ value: "F01", text: "F01" }],
      });
    },

    _resetExportCsv() {
      let vm = this;
      vm.setExportData([]);
      vm.setExportDataTitle([]);
      vm.setExportFileName("");
    },

    _setExportDataTitle() {
      let vm = this;
      let exportDataTitle = [];
      vm.operateLogDataFields.forEach((data) => {
        exportDataTitle.push(data.label);
      });
      vm.setExportDataTitle(exportDataTitle);
    },
  },
  destroyed() {
    this.clearStacksData();
    this.clearQueryResultValue();
    this.clearOperateLogDataItem();
  },
};
</script>

