<template>
    <b-col
        xl="1"
        lg="12"
        md="12"
        sm="12"
        class="featureListBar customize_scrollbar publicBlueBG-level2"
        id="featureListBar"
        ref="featureListBar"
    >
        <b-icon
            icon="list"
            scale="1.5"
            style="color: blue"
            class="mt-2 feature-list-icon"
            @click.prevent="toggleSideBarStatus"
        ></b-icon>
        <b-row class="edoc-feature-list">
            <b-col
                xl="12"
                lg="3"
                md="3"
                class="mt-2"
                v-for="(item, key, index) in featureListBarData.body"
                :key="index"
            >
                <router-link
                    :to="{
                        name: key,
                        params: {
                            SYSTEM: $route.params.SYSTEM,
                        },
                    }"
                    tag="button"
                    class="btn"
                    style="padding: 0 !important"
                    :disabled="item.hasOwnProperty('collapse')"
                >
                    <p
                        :aria-expanded="
                            toggleCollapseID == key ? 'true' : 'false'
                        "
                        aria-controls="collapse-4"
                        v-b-toggle="'collapse-' + key"
                        class="font-weight-bold"
                        :class="{
                            collapsed: toggleCollapseID == key,
                            titlechoose:
                                clickChild['child_name'].split('_')[0] == key,
                            inside_click:
                                !item.hasOwnProperty('collapse') &&
                                clickChild['child_name'].split('_')[0] == key,
                        }"
                    >
                        {{ item.title }}
                        <template v-if="item.hasOwnProperty('collapse')">
                            <b-icon
                                class="arrowup"
                                icon="caret-up-fill"
                            ></b-icon>
                            <b-icon
                                class="arrowdown"
                                icon="caret-down-fill"
                            ></b-icon>
                        </template>
                    </p>
                </router-link>
                <template v-if="item.hasOwnProperty('collapse')">
                    <b-collapse
                        :id="'collapse-' + key"
                        :ref="'collapse-' + key"
                        class="mt-2"
                        v-model="collapseIDList[key]"
                    >
                        <router-link
                            :to="{
                                name: key,
                                params: {
                                    SYSTEM: $route.params.SYSTEM,
                                },
                            }"
                        >
                            <b-button-group vertical>
                                <div
                                    v-for="(item2, index2) in item.collapse[
                                        'button-group'
                                    ]['v-for']"
                                    :key="index2"
                                    @click="
                                        setClickChild({
                                            child_name: key + '_' + item2,
                                            data:
                                                item.collapse['button-group']
                                                    .click.data,
                                        })
                                    "
                                    class="insidebtn"
                                    :class="{
                                        inside_click:
                                            clickChild['child_name'] ==
                                            key + '_' + item2,
                                    }"
                                >
                                    <template
                                        v-if="
                                            item.collapse[
                                                'button-group'
                                            ].content.hasOwnProperty(
                                                'condition'
                                            )
                                        "
                                    >
                                        <template
                                            v-if="
                                                Number.isInteger(
                                                    item.collapse[
                                                        'button-group'
                                                    ].content.condition
                                                )
                                            "
                                        >
                                            {{
                                                item.collapse["button-group"]
                                                    .content.conversiontable[
                                                    item2
                                                ][
                                                    item.collapse[
                                                        "button-group"
                                                    ].content.condition
                                                ]
                                            }}
                                        </template>
                                        <template v-else>
                                            {{
                                                item.collapse[
                                                    "button-group"
                                                ].content.conversiontable[
                                                    item2
                                                ].join(
                                                    item.collapse[
                                                        "button-group"
                                                    ].content.condition.split(
                                                        "."
                                                    )[1]
                                                )
                                            }}
                                        </template>
                                    </template>
                                    <template v-else>{{ item2 }}</template>
                                </div>
                            </b-button-group>
                        </router-link>
                    </b-collapse>
                </template>
            </b-col>
        </b-row>
    </b-col>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
    name: "modal",
    data() {
        return {};
    },
    computed: {
        ...mapGetters({
            featureMainData: "featuremain/get_featureMainData",
            featureListBarData: "featurelistbar/get_featureListBarData",
            clickChild: "featurelistbar/get_clickChild",
            toggleCollapseID: "featurelistbar/get_toggleCollapseID",
            collapseIDList: "featurelistbar/get_collapseIDList",
            clickRoute: "featuremain/get_clickRoute",
        }),
    },
    mounted() {
        console.log(this.featureListBarData);
        this.$root.$emit(
            "bv::toggle::collapse",
            "collapse-" + this.toggleCollapseID
        );
    },
    methods: {
        ...mapActions({
            setClickChild: "featurelistbar/set_clickChild",
            toggleSideBarStatus: "featuresidebar/toggle_sideBarStatus",
        }),

        _cardBodyTextStyle(element) {
            if (
                element.hasOwnProperty("style") &&
                element["style"].hasOwnProperty("card-body-text")
            ) {
                return element["style"]["card-body-text"];
            }
        },

        _cardBodyImgStyle(element) {},
    },
};
</script>

