            var vm = this;
            if (vm.user.account === "") {
                vm.setSetTimeOutAlertMsg("帳號尚未輸入");
                vm.toggleSetTimeOutAlertModal();
                return;
            }
            var obj = {};
            let temp_account_config = {
                iot: "IOT",
                paas: "SAPIDOSYSTEM",
                chunzu: "IOT",
                oqa: "IOT",
            };
            if (vm.user.account == "oqa") {
                obj.accesslist = {
                    logging: { authority: false },
                    edoc: { authority: true },
                    operation: { authority: false },
                    mes: { authority: false },
                    sensor: { authority: false },
                    profile: { authority: false },
                    bigdata: { authority: false },
                };
            } else if (
                vm.user.account == "paas" ||
                vm.user.account == "chunzu" ||
                vm.user.account == "iot"
            ) {
                obj.accesslist = {
                    bigdata: { authority: true },
                    edoc: { authority: true },
                    logging: { authority: true },
                    mes: { authority: true },
                    operation: { authority: true },
                    profile: { authority: true },
                    sensor: { authority: true },
                };
            } else {
                vm.setSetTimeOutAlertMsg("帳號錯誤");
                vm.toggleSetTimeOutAlertModal();
                return;
            }
            vm.toggleAlertModal(true);

            var params = {};
            params["url"] =
